import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Form, Button, Row, Col } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import Message from "../components/Message";
import Loader from "../components/Loader";
import FormContainer from "../components/FormContainer";
import { login } from "../actions/userActions";
import "./LoginScreenStyle.css";

const LoginScreen = ({ location, history }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);
  const { loading, error, userInfo } = userLogin;

  const redirect = location.search ? location.search.split("=")[1] : "/";

  useEffect(() => {
    if (userInfo) {
      history.push(redirect);
    }
  }, [history, userInfo, redirect]);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(login(email, password));
  };

  return (
    <div className="logn_box">
      <FormContainer>
        <h1 style={{textAlign:"center"}}>Deny Design</h1>
        <h3 style={{textAlign:"center",fontWeight:"normal"}}>WHOLESALE LOGIN</h3>
        {error && <Message variant="danger">{error}</Message>}
        {loading && <Loader />}
        <Form onSubmit={submitHandler}>
          <Form.Group controlId="email">
            <Form.Label>Email Address</Form.Label>
            <Form.Control
              type="email"
              placeholder="Enter email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId="password">
            <Form.Label>Password </Form.Label>
            <Form.Control
              type="password"
              placeholder="Enter password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            ></Form.Control>
          </Form.Group>
          <br />
          <Link>FORGOT PASSWORD?</Link>
          <br />
          <Button type="submit" variant="primary" className="lgn_btn">
            LET'S GO!
          </Button>
        </Form>
        <br />
        <Row className="py-3">
          <Col style={{textAlign:"center"}}>
            Not a WholeSale Partner?
            
            <Row className="lgn_btn_row" style={{width:"450px",marginTop:"10px",marginLeft:"-50px"}}>
              <Col>
                <Button type="submit" variant="primary" className="brw_btn" style={{height:"100%",width:"180px", fontSize:"15px"}}>
                  Keep Browsing!
                </Button>
              </Col>
              <Col>
                <Button type="submit" variant="primary" className="rgr_btn">
                  <Link
                    to={
                      redirect ? `/register?redirect=${redirect}` : "/register"
                    }
                    style={{
                      color: "#fff",
                      textTransform: "uppercase",
                      textDecoration: "none",
                    }}
                   exact>
                    Apply Here
                  </Link>
                </Button>
              </Col>
            </Row>
          </Col>
        </Row>
      </FormContainer>
    </div>
  );
};

export default LoginScreen;
